@import url('https://fonts.googleapis.com/css2?family=Amita:wght@400;700&family=Diphylleia&family=Gowun+Dodum&display=swap');

@keyframes fadeInOut {
  0% {
    opacity: 0.8;
    background-color: black;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.6;
    background-color: black;
  }
}

body,
#root {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  margin: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  h1,
  h4,
  span,
  div,
  form,
  input,
  label,
  select,
  button,
  span {
    font-family: "Gowun Dodum", pretendard variable, Pretendard, noto sans kr, malgun gothic, sans-serif;
  }

  h1 {
    font-family: "Diphylleia", serif;

    text-align: center;
    font-weight: 400;
    font-size: 40px;
    margin: 0px;

  }


  h4 {
    font-size: 22px;
    margin: 0;
    font-weight: 400;
  }

  div,
  label,
  input,
  select,
  form {
    font-size: 20px;
  }

  #detailBtn:hover {
    background: white;
    color: black;
    cursor: pointer !important;
  }

  #portfolioBtn:hover {
    cursor: not-allowed !important;
  }
}